.header {
    flex-direction: column;
    background-color: #d3cdf985;
    display: flex;
    row-gap: 0;
}

.closeBtn {
    align-self: end;
}

.icon {
    width: 50px;
    height: 50px;
    margin-bottom: 15px;
}

.headerTitle {
    font-size: 16px;
}

.orderInfoBlock {
    padding: 10px;
}

.productsTitles {
    display: grid;
    grid-template-columns: 80px 1.5fr 1fr 1fr;
    align-items: center;
    column-gap: 20px;
    justify-items: center;
    grid-template-areas: "product product quantity subtotal";
    @media (max-width: 478px) {
        grid-template-columns: 1.5fr 0.5fr 1fr;
    }
}

.productsTitles li {
    &:first-child {
        grid-area: product;
        justify-self: start;
    }

    &:nth-child(2) {
        grid-area: quantity;
    }

    &:nth-child(3) {
        grid-area: subtotal;
    }
}

.productsTitles {
    color: #6c68b0;
    margin-bottom: 15px;
    position: relative;
    padding-bottom: 10px;
}

.orderDetailsTitle {
    position: relative;
    font-family: "Alumatica", sans-serif;
    font-weight: 700;
    margin-bottom: 20px;
    font-size: 18px;
    &::after {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        content: "";
        width: 100%;
        height: 1px;
        background-color: #7789d64a;
    }
}

.customerDataList {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 10px;
    row-gap: 10px;
    margin-bottom: 40px;
    font-size: 15px;

    @media (max-width: 725px) {
        grid-template-columns: 1fr;
        column-gap: 0;
    }
}

.filedName {
    color: #6c68b0;
}

.total,
.discount {
    font-family: "Alumatica", sans-serif;
    font-weight: 700;
    margin-bottom: 20px;
    font-size: 18px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 0;
}

.discount {
    font-size: 16px;
    margin-top: 40px;
    margin-bottom: 10px;
    font-weight: 400;
}

.btn {
    padding: 12px 0 10px;
    display: flex;
    width: 30%;
    border-radius: 25px;
    background-color: rgb(190, 200, 244);
    justify-content: center;
    margin: 0 auto;
    &:hover {
        background-color: rgb(88, 110, 205);
        color: #fff;
    }
}
